.Overrall-Stats {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 40%;
  height: 100%;
}

.Overrall-Stats .Card div h3 {
  width: 90px;
  text-align: center;
  margin-top: -3em;
}

.Overrall-Stats .Card div h3 span {
  display: block;
  font-size: 1rem;
}

.Overrall-Stats .Card div h3 span:last-of-type {
  color: rgba(255, 255, 255, 0.2);
  font-size: 0.75rem;
  margin-top: 0.2em;
}

.Overrall-Stats .Card {
  position: relative;
  /* width: 55%; */
  padding: 1em;
  border-radius: 10px;
  background-color: #20254b;
}

.Overrall-Stats .Card .donutchart {
  width: 100%;
  margin-top: 1em;
}

.Overrall-Stats .Card .donutchart svg text {
  font-size: 2rem;
}

.Overrall-Stats .Card footer {
  position: absolute;
  bottom: 2em;
}

.Overrall-Stats .Card img {
  height: 18px;
}

.Overrall-Stats .Card h5 {
  /* color: rgba(255, 255, 255, 0.25); */
  color: #007a4d;
  margin: 0.5em 0;
}

.Overrall-Stats .Card h4 {
  color: #fff;
}

.Overrall-Stats > .Card {
  width: 47%;
  height: 100%;
  padding: 2em;
}

.Overrall-Stats > div:last-of-type {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
}

.Overrall-Stats div:last-of-type .Card {
  height: 90px;
}
