.National-LineChart {
  /* display: flex; */

  width: 58.5%;
  height: 100%;
  background-color: #20254b;
  border-radius: 10px;
}

.National-LineChart .linechart {
  width: 100%;
  /* margin: 0.8em auto 0; */
  /* padding-left: 1em; */
}

/* AXES */
/* ticks */
.National-LineChart .axis line {
  /* stroke: #706f6f; */
  stroke-width: 0.5;
  shape-rendering: crispEdges;
}

/* axis contour */
.National-LineChart .axis .National-LineChart path {
  stroke: #706f6f;
  stroke-width: 0.7;
  shape-rendering: crispEdges;
}

/* axis text */
.National-LineChart .axis text {
  /* fill: #2b2929; */
  /* font-family: Georgia; */
  font-size: 120%;
}

/* LINE CHART */
.National-LineChart path {
  fill: none;
  stroke: #fff;
}

/* LINE CHART */
.National-LineChart path.line-0 {
  fill: none;
  /* stroke: #de3831; */
}

.National-LineChart path.line-1 {
  fill: none;
  stroke: #007a4d;
  /* stroke-dasharray: 2; */
}

.National-LineChart path.line-2 {
  fill: none;
  stroke: #ffb612;
  /* stroke-dasharray: 6; */
}

.National-LineChart path.line-3 {
  fill: none;
  stroke: #706f6f;
  stroke-width: 0.7;
  /* stroke-dasharray: 6; */
}

.serie_label {
  fill: #fff;
  /* font-family: Georgia; */
  font-size: 80%;
}
